import Question from "@/models/Question";
import api from "@/core/Api";

/**
 * Template class represents a template entity.
 *
 * @class
 */
class Template {
  /**
   * Create a new Template instance.
   *
   * @constructor
   */
  constructor() {
    this.types = [];
    this.questions = [];
  }

  /**
   * Generate a new question based on the given type.
   *
   * @param {Object} data - The type data.
   * @param {boolean} isType - Is data a question isType true otherwise false.
   * @returns {Question} - The generated question.
   */
  generateQuestion(data, isType = true) {
    /** @var {Object} newData */
    let newData = {
      ...data,
      order: this.questions.length + 1,
    };

    if (
      data.component === "InputMultipleRadioGroup" &&
      (data?.options?.related_id === null ||
        typeof data?.options?.related_id === "undefined") &&
      (data?.options?.related_to === null ||
        typeof data?.options?.related_to === "undefined")
    ) {
      /** @var {number[]} relatedIds */
      let relatedIds = [];

      this.questions.forEach((question) => {
        if (typeof question.getOption("related_id") === "number") {
          relatedIds.push(question.getOption("related_id"));
        }
      });

      if (typeof newData.options !== "object" || newData.options === null) {
        newData.options = {};
      }

      newData.options.related_id = relatedIds.length
        ? Math.max(...relatedIds) + 1
        : 1;
    }

    return new Question(newData, isType);
  }

  /**
   * Add a question to the template.
   *
   * @param {Object} data - The question data.
   * @param {boolean} isType - Is data a question isType true otherwise false.
   */
  addQuestion(data, isType = true) {
    let question = this.generateQuestion(data, isType);

    this.questions.push(question);

    this.reOrder();
  }

  /**
   * Add a related question to the template.
   *
   * @param {Object} data - The question data.
   */
  addRelatedQuestion(data) {
    let newData = { ...data };

    if (typeof newData.options !== "object" || newData.options === null) {
      newData.options = {};
    }

    newData.options = {
      ...data.options,
      related_id: undefined,
      related_to: data.options.related_id,
    };

    let question = this.generateQuestion(newData, false);

    this.questions.push(question);

    this.reOrder();
  }

  /**
   * Get related questions by related id.
   *
   * @param {number|string} related_id - The question relation id.
   * @returns {Question[]} - The array of related questions.
   */
  getRelatedQuestions(related_id) {
    return this.questions.filter((question) => {
      return (
        parseInt(question.getOption("related_id")) === parseInt(related_id) ||
        parseInt(question.getOption("related_to")) === parseInt(related_id)
      );
    });
  }

  /**
   * Get related questions by related id.
   *
   * @param {number|string} related_id - The question relation id.
   * @returns {Question} - The array of related questions.
   */
  getRelatedQuestion(related_id) {
    return this.questions.find((question) => {
      return (
        parseInt(question.getOption("related_id")) === parseInt(related_id)
      );
    });
  }

  /**
   * Get only related questions by related id.
   *
   * @param {number|string} related_id - The question relation id.
   * @returns {Question[]} - The array of related questions.
   */
  getOnlyRelatedQuestions(related_id) {
    return this.questions.filter((question) => {
      return (
        parseInt(question.getOption("related_to")) === parseInt(related_id)
      );
    });
  }

  /**
   * Add questions to the template.
   *
   * @param {array} questions - The questions.
   * @param {boolean} isType - Is data a question isType true otherwise false.
   * @param {boolean} clear - Clear current question data.
   */
  saveQuestions(questions, isType = true, clear = true) {
    if (clear) {
      this.questions = [];
    }

    questions.forEach((question) => {
      if (question?.options?.related_to) {
        let relatedQuest = this.getRelatedQuestion(
          question?.options?.related_to
        );
        question.values = relatedQuest.values;
      }

      this.addQuestion(question, isType);
    });
  }

  /**
   * Remove a question from the template at the specified index.
   *
   * @param {number} index - The index of the question to be removed.
   */
  removeQuestion(index) {
    let question = this.questions[index];

    if (question.getOption("related_id")) {
      let related_id = parseInt(question.options.related_id);

      this.questions = this.questions.filter((quest) => {
        return (
          parseInt(quest.getOption("related_id")) !== related_id &&
          parseInt(quest.getOption("related_to")) !== related_id
        );
      });
    } else {
      this.questions.splice(index, 1);
    }

    this.reOrder();
  }

  /**
   * Remove a question from the template at the specified order.
   *
   * @param {number} order - The order of the question to be removed.
   */
  removeQuestionByOrder(order) {
    let index = this.questions.findIndex((question) => {
      return question.order === order;
    });

    this.removeQuestion(index);
  }

  /**
   * Get all questions in the template.
   *
   * @returns {Question[]} - The array of questions.
   */
  getQuestions() {
    return this.questions;
  }

  /**
   * Reorder the questions based on their index.
   */
  reOrder() {
    /** @var {Question[]} sortedQuestions */
    const sortedQuestions = [];

    this.questions.forEach((question) => {
      if (!question.getOption("related_to")) {
        sortedQuestions.push(question);
      }

      if (question.getOption("related_id")) {
        this.getOnlyRelatedQuestions(question.getOption("related_id")).forEach(
          (relatedQuestion) => {
            sortedQuestions.push(relatedQuestion);
          }
        );
      }
    });

    this.questions = sortedQuestions;

    this.questions.forEach((question, index) => (question.order = index + 1));
  }

  /**
   * Get all types from the server.
   */
  getTypes() {
    api.get("/question-types").then(({ data }) => {
      this.types = data;
    });
  }

  /**
   * Add new question type.
   *
   * @param {object} type
   */
  addType(type) {
    this.types.push(type);
  }

  /**
   * Remove a type from the template and its associated questions.
   *
   * @param {number} id - The ID of the type to be removed.
   */
  removeType(id) {
    const index = this.types.findIndex((type) => type.id === id);

    this.types.splice(index, 1);

    this.questions = this.questions.filter(
      (question) => question.question_type_id !== id
    );
    this.reOrder();
  }

  /**
   * Delete a type from the server and remove it from the template.
   *
   * @param {number} id - The ID of the type to be deleted.
   * @returns {Promise} - A promise that resolves with the response.
   */
  deleteType(id) {
    return new Promise((resolve, reject) => {
      api
        .post(`/question-type/delete/${id}`)
        .then((response) => {
          this.removeType(id);
          resolve(response);
        })
        .catch((error) => {
          reject(error);
        });
    });
  }

  /**
   * Edit a question type to the template.
   *
   * @param {number} id - The ID of the type to be edited.
   * @param {Object} data - The question type data.
   * @returns {void}
   */
  editType(id, data) {
    let index = this.types.findIndex((type) => {
      return type.id === id;
    });

    this.types[index] = data;
  }
}

export default Template;
