<template>
  <div class="grid grid-cols-5">
    <div class="col-span-2 flex flex-col gap-3">
      <draggable
        :list="question.get('values')"
        :item-key="(item, index) => index"
        :component-data="{
          tag: 'div',
          type: 'transition-group',
          name: !drag ? 'flip-list' : null,
        }"
        v-bind="dragOptions"
        handle=".handle"
        class="grow space-y-4"
        @start="drag = true"
        @end="drag = false"
      >
        <template #item="{ element, index }">
          <div class="flex gap-2">
            <label class="inline-flex items-center space-x-2">
              <input
                class="h-4 w-4 rounded border-slate-400/70 form-checkbox is-basic checked:border-slate-500 checked:bg-slate-500 hover:border-slate-500 focus:border-slate-500 dark:border-navy-400 dark:checked:bg-navy-400"
                type="checkbox"
              />
            </label>

            <input
              class="w-full border-b border-slate-300 bg-transparent pb-1 placeholder:text-slate-400/70 form-input hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
              type="text"
              :value="element.label"
              @input="question.setValue(index, 'label', $event.target.value)"
            />

            <input
              class="w-20 border-b border-slate-300 bg-transparent pb-1 placeholder:text-slate-400/70 form-input hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
              type="text"
              :value="element.score"
              @input="question.setValue(index, 'score', $event.target.value)"
            />

            <div class="flex flex-row">
              <div
                class="cursor-move text-slate-400 transition-colors handle hover:text-slate-800"
              >
                <i class="fa fa-fw fa-align-justify"></i>
              </div>
              <div
                class="cursor-pointer text-slate-400 transition-colors close hover:text-slate-800"
                @click="question.removeValue(index)"
              >
                <i class="fa fa-fw fa-times"></i>
              </div>
            </div>
          </div>
        </template>
      </draggable>

      <div class="text-end">
        <div
          class="cursor-pointer text-slate-400 transition-colors close hover:text-slate-800"
          @click="question.addValue($t('labels.option'))"
        >
          <span class="mr-1">{{ $t("labels.add_answer") }}</span>
          <i class="fa fa-fw fa-plus"></i>
        </div>
      </div>
    </div>
  </div>

  <question-options
    :template="template"
    :question="question"
  ></question-options>
</template>

<script>
import Template from "@/models/Template";
import Question from "@/models/Question";
import QuestionOptions from "@/components/QuestionInputs/QuestionOptions.vue";
import draggable from "vuedraggable";

export default {
  name: "InputRadioGroup",
  components: { QuestionOptions, draggable },
  props: {
    template: {
      type: Template,
      required: true,
    },
    question: {
      type: Question,
      required: true,
    },
  },
  data() {
    return {
      drag: false,
    };
  },
  computed: {
    dragOptions() {
      return {
        animation: 200,
        disabled: false,
        ghostClass: "ghost",
      };
    },
  },
};
</script>
