import api from "@/core/Api";

/**
 * Question class represents a question entity.
 *
 * @class
 */
class Question {
  /**
   * Create a new Question instance.
   *
   * @constructor
   * @param {Object} data - The data object containing question information.
   * @param {number} data.id - The ID of the question type.
   * @param {number} data.question_type_id - The ID of the question type.
   * @param {number} data.main_question_type_id - The main type ID of the question.
   * @param {string} data.component - The component type of the question.
   * @param {string} data.label - The name of the question.
   * @param {string} data.description - The description of the question.
   * @param {string|undefined} data.label - The label of the question.
   * @param {boolean} data.required - Indicates if the question is required.
   * @param {any} data.value - The value of the question.
   * @param {Array|null} data.values - The values of the question.
   * @param {Object} data.options - The options of the question.
   * @param {number} data.order - The order of the question.
   * @param {boolean} isType
   */
  constructor(data, isType = true) {
    this.id = isType ? null : data.id;
    this.question_type_id = isType ? data.id : data.question_type_id;
    this.main_question_type_id = data.main_question_type_id;
    this.component = data.component;
    this.component_name = data.label;
    this.component_description = data.description;
    this.label = data.label;
    this.description = data.description;
    this.required = data.required ?? false;
    this.order = data.order;
    this.value = data.value;

    if (data?.options?.related_to) {
      this.values = data.values;
      this.options = data.options;
    } else {
      this.values = Array.isArray(data.values)
        ? JSON.parse(JSON.stringify(data.values))
        : data.values;
      this.options =
        data.options !== null && typeof data.options === "object"
          ? JSON.parse(JSON.stringify(data.options))
          : {};
    }

    this.show_options = false;
  }

  /**
   * Get the value of a property by key.
   *
   * @param {string} key - The key of the property.
   * @returns {*} - The value of the property.
   */
  get(key) {
    return this[key];
  }

  /**
   * Set the value of a property by key.
   *
   * @param {string} key - The key of the property.
   * @param {*} val - The value to be set.
   */
  set(key, val) {
    this[key] = val;
  }

  /**
   * Get the option value by key.
   *
   * @param {string} key - The key of the option.
   * @param {any} defaultValue - Default value for returning.
   * @returns {*} - The value of the option.
   */
  getOption(key, defaultValue = null) {
    if (this.options && typeof this.options[key] !== "undefined") {
      return this.options[key];
    }

    return defaultValue;
  }

  /**
   * Set the option value by key.
   *
   * @param {string} key - The key of the option.
   * @param {*} val - The value to be set.
   */
  setOption(key, val = null) {
    if (val === null) {
      delete this.options[key];
      return;
    }

    this.options[key] = val;
  }

  /**
   * Toggle the option value by key.
   *
   * @param {string} key - The key of the option.
   */
  toggleOption(key) {
    if (typeof this.options[key] === "undefined") {
      this.options[key] = true;
    } else {
      this.options[key] = !this.options[key];
    }
  }

  /**
   * Get the value by key.
   *
   * @param {number} index - The index of the value.
   * @param {string} key - The key of the value.
   * @param {any} defaultValue - Default value for returning.
   * @returns {*} - The value.
   */
  getValue(index, key, defaultValue = null) {
    if (typeof this.values[index][key] !== "undefined") {
      return this.values[index][key];
    }

    return defaultValue;
  }

  /**
   * Set the value by key.
   *
   * @param {number} index - The index of the value.
   * @param {string} key - The key of the value.
   * @param {*} val - The value to be set.
   */
  setValue(index, key, val = null) {
    if (val === null) {
      delete this.values[key];
      return;
    }

    this.values[index][key] = val;
  }

  /**
   * Add a new value.
   *
   * @param {string} label - The label to be added.
   * @param {string|number|null} score - The label to be added.
   */
  addValue(label, score = null) {
    this.values.push({
      label,
      score: score ?? this.values.length + 1,
    });
  }

  /**
   * Remove the value at the specified index.
   *
   * @param {number} index - The index of the value to be removed.
   */
  removeValue(index) {
    this.values.splice(index, 1);
  }

  /**
   * Get the show options property.
   *
   * @returns {boolean} - The value of show options.
   */
  getOptionShowAble() {
    return this.show_options;
  }

  /**
   * Toggle the show options property.
   */
  toggleOptionShowAble() {
    this.show_options = !this.show_options;
  }

  /**
   * Check if the question is updatable.
   *
   * @returns {boolean} - True if the question is updatable, false otherwise.
   */
  isUpdatable() {
    return !(
      typeof this.main_question_type_id === "undefined" ||
      this.main_question_type_id === null
    );
  }

  /**
   * Store the question type.
   *
   * @returns {Promise} - A promise that resolves with the response.
   */
  storeType() {
    return new Promise((resolve, reject) => {
      api
        .post("/question-type/store", this)
        .then((response) => {
          this.question_type_id = response.data.id;
          this.main_question_type_id = response.data.main_question_type_id;

          resolve(response);
        })
        .catch((error) => {
          reject(error);
        });
    });
  }

  /**
   * Update the question type.
   *
   * @returns {Promise} - A promise that resolves with the response.
   */
  updateType() {
    return new Promise((resolve, reject) => {
      api
        .post(`/question-type/update/${this.question_type_id}`, this)
        .then((response) => {
          resolve(response);
        })
        .catch((error) => {
          reject(error);
        });
    });
  }
}

export default Question;
