<template>
  <div
    class="relative rounded border border-slate-200 p-4 dark:border-navy-600"
  >
    <div class="absolute top-px right-8 flex gap-3">
      <button
        type="button"
        class="text-slate-400 transition-colors hover:text-slate-800"
        :class="{ 'text-slate-800': question.getOptionShowAble() }"
        @click="question.toggleOptionShowAble()"
      >
        <i class="fa fa-lg fa-gear"></i>
      </button>
      <div
        class="cursor-move text-slate-400 transition-colors handle hover:text-slate-800"
      >
        <i class="fa fa-lg fa-align-justify"></i>
      </div>
      <button
        type="button"
        class="text-slate-400 transition-colors close hover:text-slate-800"
        @click="this.template.removeQuestion(index)"
      >
        <i class="fa fa-lg fa-times"></i>
      </button>
    </div>

    <div class="space-y-4" :class="{ 'pt-4': !labelShowable }">
      <div v-if="labelShowable">
        <input
          class="w-full border-b border-slate-300 bg-transparent pb-2 placeholder:text-slate-400/70 form-input hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
          placeholder="Soruyu giriniz."
          type="text"
          :value="question.label"
          @input="question.set('label', $event.target.value)"
        />
      </div>

      <component
        :is="question.component"
        :template="template"
        :question="question"
      ></component>
    </div>
  </div>
</template>

<script>
import Template from "@/models/Template";
import Question from "@/models/Question";
import InputText from "@/components/QuestionInputs/Input/InputText.vue";
import InputNumber from "@/components/QuestionInputs/Input/InputNumber.vue";
import InputRadioGroup from "@/components/QuestionInputs/Input/InputRadioGroup.vue";
import InputCheckboxGroup from "@/components/QuestionInputs/Input/InputCheckboxGroup.vue";
import InputDate from "@/components/QuestionInputs/Input/InputDate.vue";
import InputMultipleRadioGroup from "@/components/QuestionInputs/Input/InputMultipleRadioGroup.vue";
import OutputDescription from "@/components/QuestionInputs/Output/OutputDescription.vue";
import OutputImage from "@/components/QuestionInputs/Output/OutputImage.vue";

export default {
  name: "QuestionInput",
  components: {
    InputText,
    InputNumber,
    InputRadioGroup,
    InputCheckboxGroup,
    InputDate,
    InputMultipleRadioGroup,
    OutputDescription,
    OutputImage,
  },
  props: {
    template: {
      type: Template,
      required: true,
    },
    question: {
      type: Question,
      required: true,
    },
    index: {
      type: [String, Number],
    },
  },
  computed: {
    labelShowable() {
      return !["InputMultipleRadioGroup"].includes(this.question.component);
    },
  },
};
</script>

<style scoped>
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.3s ease;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}
</style>
