<template>
  <div
    v-if="question.getOptionShowAble()"
    key="isOptionsShowAble"
    class="rounded border border-slate-200 p-4 space-y-3 dark:border-navy-600"
  >
    <div class="grid grid-cols-12 items-center gap-3">
      <div class="col-span-1 text-end">
        <label class="font-medium text-slate-600 mb-1.5 dark:text-navy-100">{{
          $t("attributes.required")
        }}</label>
      </div>
      <div class="col-span-11">
        <label class="inline-flex items-center space-x-2">
          <input
            class="rounded border-slate-400/70 form-checkbox is-basic h-3.5 w-3.5 checked:border-slate-500 checked:bg-slate-500 hover:border-slate-500 focus:border-slate-500 dark:border-navy-400 dark:checked:bg-navy-400"
            type="checkbox"
            :checked="question.get('required')"
            @input="question.set('required', !question.get('required'))"
          />
        </label>
      </div>
    </div>

    <div class="grid grid-cols-12 items-center gap-3">
      <div class="col-span-1 text-end">
        <label class="font-medium text-slate-600 mb-1.5 dark:text-navy-100">{{
          $t("attributes.order")
        }}</label>
      </div>
      <div class="col-span-2">
        <input
          type="number"
          disabled
          class="h-8 w-full rounded-lg border border-slate-300 bg-transparent px-2 py-1 text-xs+ placeholder:text-slate-400/70 form-input hover:border-slate-400 focus:border-primary disabled:pointer-events-none disabled:select-none disabled:bg-zinc-100 dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent dark:disabled:bg-navy-600"
          :value="question.get('order')"
        />
      </div>
    </div>

    <div class="grid grid-cols-12 items-center gap-3">
      <div class="col-span-1 text-end">
        <label class="font-medium text-slate-600 mb-1.5 dark:text-navy-100">{{
          $t("attributes.description")
        }}</label>
      </div>
      <div class="col-span-11">
        <input
          class="form-input h-8 w-full rounded-lg border border-slate-300 bg-transparent px-2 py-1 text-xs+ placeholder:text-slate-400/70 hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
          type="text"
          :value="question.get('description')"
          @input="question.set('description', $event.target.value)"
        />
      </div>
    </div>

    <slot></slot>

    <div class="grid grid-cols-12 items-center gap-3">
      <div class="col-start-2 col-end-13">
        <v-button color="info" @click="$refs.modal.toggle()"
          >{{ $t("modal.titles.save_question") }}
        </v-button>
      </div>
    </div>
  </div>

  <v-modal ref="modal">
    <template #title>{{ $t("modal.titles.save_question") }}</template>
    <template #body>
      <div class="text-start space-y-3">
        <div>
          <label for="textComponentName" class="font-medium">{{
            $t("attributes.question_name")
          }}</label>
          <input
            id="textComponentName"
            class="form-input mt-2 w-full rounded-lg border border-slate-300 bg-transparent px-2 py-1 text-xs+ placeholder:text-slate-400/70 hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
            type="text"
            :value="question.get('component_name')"
            @input="question.set('component_name', $event.target.value)"
          />
        </div>

        <div>
          <label for="textComponentDescription" class="font-medium">{{
            $t("attributes.question_description")
          }}</label>
          <input
            id="textComponentDescription"
            class="form-input mt-2 w-full rounded-lg border border-slate-300 bg-transparent px-2 py-1 text-xs+ placeholder:text-slate-400/70 hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
            type="text"
            :value="question.get('component_description')"
            @input="question.set('component_description', $event.target.value)"
          />
        </div>

        <div v-if="question.isUpdatable()" key="isQuestionTypeCustom">
          <small
            >Yeni bir soru tipi olarak kaydetmek için lütfen farklı
            kaydedin.</small
          >
        </div>
      </div>
    </template>
    <template #buttons>
      <div
        v-if="question.isUpdatable()"
        key="isQuestionTypeCustom"
        class="space-x-3"
      >
        <v-button color="info" @click="storeType"
          >{{ $t("labels.save_as") }}
        </v-button>
        <v-button color="success" @click="updateType"
          >{{ $t("labels.submit") }}
        </v-button>
      </div>
      <div v-else>
        <v-button color="success" @click="storeType"
          >{{ $t("labels.submit") }}
        </v-button>
      </div>
    </template>
  </v-modal>
</template>

<script>
import Template from "@/models/Template";
import Question from "@/models/Question";
import Toast from "@/core/Toast";

export default {
  name: "QuestionOptions",
  props: {
    template: {
      type: Template,
      required: true,
    },
    question: {
      type: Question,
      required: true,
    },
  },
  methods: {
    storeType() {
      this.question
        .storeType()
        .then((response) => {
          Toast.fire(this.$t("response.success.store"), "success");
          this.template.addType(response.data);
          this.$refs.modal.toggle();
        })
        .catch((error) => {
          Toast.fire(
            this.$t("response.errors." + error.response.status),
            "error"
          );
        });
    },
    updateType() {
      this.question
        .updateType()
        .then((response) => {
          Toast.fire(this.$t("response.success.update"), "success");
          this.template.editType(this.question.question_type_id, response.data);
          this.$refs.modal.toggle();
        })
        .catch((error) => {
          Toast.fire(
            this.$t("response.errors." + error.response.status),
            "error"
          );
        });
    },
  },
};
</script>
