<template>
  <breadcrumb-list :items="breadcrumbs"></breadcrumb-list>

  <div class="grid grid-cols-10 gap-4 sm:gap-5 lg:gap-6">
    <div class="col-span-10 space-y-6 xl:col-span-2">
      <div class="p-4 card sm:p-5">
        <div>
          <label for="textName" class="font-medium">{{
            $t("attributes.survey_name")
          }}</label>
          <div class="mt-1.5">
            <input
              v-model="form.name"
              id="textName"
              name="template_name"
              type="text"
              class="w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 placeholder:text-slate-400/70 form-input peer hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
            />
          </div>
        </div>

        <div class="mt-3">
          <label for="textDescription" class="font-medium">{{
            $t("attributes.survey_description")
          }}</label>
          <div class="mt-1.5">
            <textarea
              v-model="form.description"
              id="textDescription"
              class="w-full resize-y rounded-lg border border-slate-300 bg-transparent px-3 py-2 placeholder:text-slate-400/70 form-textarea form-input hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
              rows="3"
            />
          </div>
        </div>
      </div>

      <div class="sticky top-[76px] space-y-6">
        <div class="p-4 card sm:p-5">
          <draggable
            :list="this.template.types"
            :group="{ name: 'questions', pull: 'clone', put: false }"
            :sort="false"
            :clone="(type) => template.generateQuestion(type)"
            item-key="name"
            class="space-y-4"
          >
            <template #item="{ element }">
              <div class="rounded border border-slate-200 dark:border-navy-600">
                <div class="flex justify-between">
                  <button
                    class="flex grow cursor-pointer flex-col p-3 text-start"
                    @click="template.addQuestion(element)"
                  >
                    <span class="font-medium">{{ element.label }}</span>
                    <small>{{ element.description }}</small>
                  </button>

                  <button
                    v-if="element.user_id !== null"
                    key="isQuestionTypeCustom"
                    type="button"
                    class="p-3 text-slate-400 transition-colors close hover:text-slate-800"
                    @click="deleteTypeModal(element.id)"
                    :title="$t('modal.titles.delete_question_type')"
                  >
                    <i class="fa fa-times fa-lg"></i>
                  </button>

                  <button
                    type="button"
                    class="p-3 text-slate-400 transition-colors close hover:text-slate-800"
                    @click="addMultipleQuestionsModal(element)"
                    :title="$t('modal.titles.add_multiple_questions')"
                  >
                    <i class="fa fa-regular fa-square-plus fa-lg"></i>
                  </button>
                </div>
              </div>
            </template>
          </draggable>
        </div>

        <div class="p-4 card sm:p-5">
          <div>
            <v-button @click="submit">{{ $t("labels.submit") }}</v-button>
          </div>
        </div>
      </div>
    </div>

    <div class="col-span-10 flex flex-col gap-5 xl:col-span-8">
      <div class="grow p-4 card sm:p-5">
        <draggable
          :list="template.questions"
          group="questions"
          item-key="order"
          :component-data="{
            tag: 'div',
            type: 'transition-group',
            name: !drag ? 'flip-list' : null,
          }"
          v-bind="dragOptions"
          handle=".handle"
          class="grow space-y-4"
          @start="drag = true"
          @end="drag = false"
          @change="template.reOrder()"
        >
          <template #item="{ element, index }">
            <question-input
              v-if="element.getOption('related_to') === null"
              :template="template"
              :question="element"
              :index="index"
              @remove="removeQuestion"
            ></question-input>
          </template>
        </draggable>
      </div>
    </div>
  </div>

  <v-modal ref="deleteQuestionModal">
    <template #title>{{ $t("modal.titles.delete_question_type") }}</template>
    <template #body>{{ $t("modal.messages.delete") }}</template>
    <template #buttons>
      <v-button color="danger" @click="deleteType"
        >{{ $t("labels.delete") }}
      </v-button>
    </template>
  </v-modal>

  <v-modal ref="addMultipleQuestionsModal">
    <template #title>{{ $t("modal.titles.add_multiple_questions") }}</template>
    <template #body>
      <div class="text-start space-y-3">
        <div>
          <label for="numberAddMultipleQuestionCount" class="font-medium">{{
            $t("attributes.question_count")
          }}</label>
          <input
            v-model="add_multiple_question_count"
            id="numberAddMultipleQuestionCount"
            class="form-input mt-2 w-full rounded-lg border border-slate-300 bg-transparent p-2 text-xs+ placeholder:text-slate-400/70 hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
            type="number"
            min="1"
            max="99"
          />
        </div>
      </div>
    </template>
    <template #buttons>
      <v-button color="success" @click="addMultipleQuestions"
        >{{ $t("labels.submit") }}
      </v-button>
    </template>
  </v-modal>
</template>

<script>
import Form from "@/core/Form";
import Template from "@/models/Template";
import Toast from "@/core/Toast";
import draggable from "vuedraggable";
import QuestionInput from "@/components/QuestionInputs/QuestionInput.vue";
import api from "@/core/Api";
import router from "@/router";

export default {
  name: "TemplateForm",
  components: { draggable, QuestionInput },
  props: {
    id: {
      type: String,
      required: false,
    },
  },
  data() {
    return {
      breadcrumbs: [
        { title: this.$t("titles.templates"), route: "templates" },
        {
          title: this.id
            ? this.$t("models.template.edit")
            : this.$t("models.template.add"),
          active: true,
        },
      ],
      drag: false,
      delete_type_id: null,
      add_multiple_question_item: null,
      add_multiple_question_count: 1,
      template: new Template(),
      form: new Form({
        name: "",
        description: "",
      }),
    };
  },
  async created() {
    this.template.getTypes();

    if (this.id) {
      await api.get(`/template/${this.id}`).then(({ data }) => {
        this.form.updateData({
          id: data.id,
          name: data.name,
          description: data.description,
          questions: this.template.questions,
        });

        this.template.saveQuestions(data.questions, false);
      });
    }
  },
  methods: {
    removeQuestion(index) {
      this.template.removeQuestion(index);
    },
    deleteTypeModal(id) {
      this.$refs.deleteQuestionModal.toggle();
      this.delete_type_id = id;
    },
    deleteType() {
      this.template
        .deleteType(this.delete_type_id)
        .then(() => {
          Toast.fire(this.$t("response.success.delete"), "success");
          this.delete_type_id = null;
          this.$refs.deleteQuestionModal.toggle();
        })
        .catch((error) => {
          Toast.fire(
            this.$t("response.errors." + error.response.status),
            "error"
          );
        });
    },
    addMultipleQuestionsModal(item) {
      this.$refs.addMultipleQuestionsModal.toggle();
      this.add_multiple_question_item = item;
    },
    addMultipleQuestions() {
      let count = this.add_multiple_question_count;

      if (count < 0 || count > 50) {
        return;
      }

      for (let i = 0; i < count; i++) {
        this.template.addQuestion(this.add_multiple_question_item);
      }

      this.add_multiple_question_item = null;
      this.add_multiple_question_count = 1;
      this.$refs.addMultipleQuestionsModal.toggle();
    },
    async submit() {
      this.form.addData("questions", this.template.questions);

      if (this.form.isUpdate()) {
        this.form
          .post("/template/update", this.form.getId())
          .then(() => {
            Toast.fire(this.$t("response.success.update"), "success");
          })
          .catch(() => {
            Toast.fire(this.form.getErrorMessage(), "error");
          });
      } else {
        this.form
          .post("/template/store")
          .then((data) => {
            Toast.fire(this.$t("response.success.store"), "success");

            this.form.updateData({
              id: data.id,
              name: data.name,
              description: data.description,
              questions: this.template.questions,
            });

            this.template.saveQuestions(data.questions, false);

            setTimeout(() => {
              router.push({
                name: "template.edit",
                params: { id: data.id },
              });
            }, 1000);
          })
          .catch(() => {
            Toast.fire(this.form.getErrorMessage(), "error");
          });
      }
    },
  },
  computed: {
    dragOptions() {
      return {
        animation: 200,
        disabled: false,
        ghostClass: "ghost",
      };
    },
  },
};
</script>

<style>
.flip-list-move {
  transition: transform 0.5s;
}

.no-move {
  transition: transform 0s;
}

.ghost {
  opacity: 0.5;
  @apply bg-amber-100;
}
</style>
